<template>
  <div id="app">
    <!-- 头部导航 -->
    <header class="header" :class="{ 'header-fixed' : headerFixed }">
    <!-- <el-row>
        <el-col :span="24">
          <el-menu default-active="5" class="el-menu-demo" mode="horizontal" @select="">
            <el-menu-item index="1">高级插件</el-menu-item>
            <el-menu-item index="2">在线商城</el-menu-item>
            <el-menu-item index="3">客户管理</el-menu-item>
            <el-menu-item index="4">系统设置</el-menu-item>
            <el-menu-item index="5">活动发布</el-menu-item>
          </el-menu>
        </el-col>
    </el-row> -->
    </header>

    <main>
      <div class="main-wrap">
        <!-- 左侧导航 -->
        <div class="main-left">
          <el-menu theme="dark"
            :default-active="currTab"
            class="el-menu-vertical-demo"
            :router="false" @select="selectMenu">
            <el-menu-item
              v-for="item in menu"
              :index="item.name"
              >
              <i class="el-icon-message"></i>{{item.name}}
            </el-menu-item>
          </el-menu>
        </div>

        <!-- 右侧主内容区 -->
        <div  class="main-right" >
          <el-tabs
            v-model="currTab"
            type="card"
            @tab-click="selectTab"
            @edit="editTab"
            style="width:100%">

              <el-tab-pane
                label="首页"
                name="home"
                :closeable="false"
              >
              <h1>首页</h1>
              </el-tab-pane>

              <el-tab-pane
                v-for="(item, index) in tabs"
                :label="item.tabName"
                :name="item.name"
                :closable="true"
              >
                <router-view class="view" :name="item.name"></router-view>
              </el-tab-pane>
          </el-tabs>
          <!-- <transition name="fade">
            <router-view class="view"></router-view>
          </transition> -->
        </div>
      </div>
    </main>
  </div>
</template>

<script>

import Vue from 'vue'
import Element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'

import $ from 'jquery'
import _ from 'lodash'

Vue.use(Element)

const menu = [
  {name : "a", url: "/a", tabName: "page a"},
  {name : "b", url: "/b", tabName: "page b"},
  {name : "activePublic", url: "/activePublic", tabName: "page activePublic"},
  {name : "activeManage", url: "/activeManage", tabName: "page activeManage"}
]

export default {
  name: 'app',
  data (){
    return {
      currTab: 'home',
      tabs: [],
      tabIndex: 2,
      menu : menu,
      active:true,
      headerFixed : true
    }
  },
  created: function(){
    // this.$router.push('/a');
    // this.$router.push('/activePublic');
  },
  methods: {
    selectMenu : function(tabIdx){
      var app = this;
      app.currTab = tabIdx;
      let curr = menu.find((o)=>{
        return o.name == tabIdx;
      })
      if (!!!_.find(app.tabs, { 'name': tabIdx })) {//不存在打开菜单
        app.addTab(curr)
      }
    },
    selectTab : function (tab) {
      var app = this;
      app.currTab = tab.name
    },
    addTab : function(tab){
      var app = this;
      app.tabs.push(tab);
    },
    editTab : function (targetName,action) {
      if (action === 'remove'){
        let tabs = this.tabs;
        let activeName = this.currTab;
        if (activeName === targetName) {
          tabs.forEach((tab, index) => {
            if (tab.name === targetName) {
              let nextTab = tabs[index + 1] || tabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        }

        this.currTab = activeName;
        this.tabs = tabs.filter(tab => tab.name !== targetName);
      }

    }
    // tabClick : function(tabItem){
    //   this.$router.push('/'+ tabItem.name);
    // }
  },
  // watch: {
  //    '$route': function (to,from) {
  //        if(to.path == '/activePublic'){
  //            this.active = true ;
  //        }else if(to.path == '/activeManage'){
  //            this.active = false ;
  //        }
  //    }
  // }
}
</script>

<style>
@import './assets/css/main.css';
</style>



